<template>
	<div>
		<div class="ym_content">
			<div class="ym_banner"></div>
			<div class="badge">
				<span></span>
				<p>业务范围</p>
			</div>
			<ul>
				<li v-for="s in getServiceList">
					<img :src="s.url"></img>
					<p>{{ s.name }}</p>
				</li>
			</ul>
		</div>
	</div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  name: 'ymContent',
  data () {
    return {
    }
  },
  computed: {
    ...mapGetters(['getServiceList'])
  }
}
</script>
<style lang="scss" scoped>
$mac:0.71;
ul{
	display: flex;
	flex-wrap:wrap;
	li{
		width: 298*$mac+px;
		height: 250*$mac+px;
		background-color: #fff;
		border-bottom: 1px solid #ddd;
		border-right:1px solid #ddd ;
		display: inline-block;
		text-align: center;
		img {
			height: 100*$mac+px;
			height: 100*$mac+px;
			margin-top: 77*$mac+px;
		}
		p{
			font-size: 20*$mac+px;
			margin-top: 10*$mac+px;
			color:#888;
		}
	}
}
  .ym_content {
  	width: 1200*$mac+px;
  	margin: 0 auto;
  	background-color: #f4f4f4;
  }
  .ym_banner {
  	background-image: url('http://p2fkgzxqv.bkt.clouddn.com/banner.png');
  	background-size: 100% 100%;
  	height: 500*$mac+px;
  }
  .badge {
  	display: flex;
  	justify-content: flex-start;
  	span {
  		height: 35*$mac+px;
  		width: 3.5px;
  		background-color: #006FAF;
  		margin: 22*$mac+px 0;
  	}
  	p {
  		margin: 27*$mac+px 0 0 12*$mac+px;
  		font-size: 25*$mac+px;
  		color: #888;
  	}
  }
</style>


