<template>
	<div>
		<div>
			<el-dialog
			  title="订单详情"
			  :visible.sync="state"
			  width="80%"
			  center>
			  <div class="dialog-content">
			  	<el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign">
				  <el-form-item label="名称">
				    <el-input v-model="formLabelAlign.name"></el-input>
				  </el-form-item>
				  <el-form-item label="活动区域">
				    <el-input v-model="formLabelAlign.region"></el-input>
				  </el-form-item>
				  <el-form-item label="活动形式">
				    <el-input v-model="formLabelAlign.type"></el-input>
				  </el-form-item>
				</el-form>
			  </div>
			  <span slot="footer" class="dialog-footer">
			    <el-button @click="handelCloseDialogVisible">取 消</el-button>
			    <el-button type="primary" @click="handelCloseDialogVisible">确 定</el-button>
			  </span>
			</el-dialog>
		</div>
	</div>
</template>
<script>
export default {
  name: 'dialogItem',
  data () {
    return {
      labelPosition: 'right',
      formLabelAlign: {
        name: '',
        region: '',
        type: ''
      }
    }
  },
  props: {
    state: Boolean
  },
  computed: {
  },
  methods: {
    handelCloseDialogVisible () {
      this.state = false
      this.$emit('handleCurrent', this.state)
    }
  }
}
</script>
<style lang="scss" scoped>
$mac:0.71;
</style>


