<template>
	<div>
		<div class="ym_content_hos">
			<div class="badge">
					<span></span>
					<p>医院内景(最少上传三张)</p>
			</div>
			<div class="ym_pic">
				<el-upload
				  action="https://jsonplaceholder.typicode.com/posts/"
				  list-type="picture-card"
				  multiple
				  :on-preview="handlePictureCardPreview"
				  :on-remove="handleRemove" class="hos_imgs">
				  <i class="el-icon-plus"></i>
				</el-upload>
				<el-dialog :visible.sync="dialogVisible" >
				  <img width="100%" :src="hospitalUrl" alt="">
				</el-dialog>
			</div>
			
			<div>
				<div class="ym_edit_hospitalProduce">
					<span>医院介绍(不得超过200字)</span>
					<!--<p @click="handelEdit">点击编辑</p>-->
					<el-switch
					  style="display: block"
					  v-model="editSwitch"
					  active-color="#13ce66"
					  inactive-color="#ff4949"
					  @change="handelEdit"
					  active-text="完成"
					  inactive-text="编辑">
					</el-switch>
				</div>
				<div class="textarea_input">
					<el-input
					  type="textarea"
					  :disabled="disabledTextArea"
					  :maxlength=200
					  :rows="10"
					  placeholder="请输入内容"
					  v-model="textareaValue">
					</el-input>
				</div>
			</div>
		    
		    <div class="badge">
					<span></span>
					<p>医院营业执照及许可证书</p>
			</div>
			<div class="ym_pic">
				<el-upload
				  action="https://jsonplaceholder.typicode.com/posts/"
				  list-type="picture-card"
				  multiple
				  :on-preview="handlePictureCardPreview2"
				  :on-remove="handleRemove2">
				  <i class="el-icon-plus"></i>
				</el-upload>
				<el-dialog :visible.sync="dialogVisible2" >
				  <img width="100%" :src="hospitalUrl2" alt="">
				</el-dialog>
			</div>
			
			<div class="badge">
					<span></span>
					<p>设备一览(至少上传三张)</p>
			</div>
			<div class="ym_pic">
				<el-upload
				  action="https://jsonplaceholder.typicode.com/posts/"
				  list-type="picture-card"
				  multiple
				  :on-preview="handlePictureCardPreview3"
				  :on-remove="handleRemove3">
				  <i class="el-icon-plus"></i>
				</el-upload>
				<el-dialog :visible.sync="dialogVisible3" >
				  <img width="100%" :src="hospitalUrl3" alt="">
				</el-dialog>
			</div>
			
			<div>
				<div class="ym_edit_hospitalProduce">
					<span>医院介绍(不得超过200字)</span>
					<!--<p @click="handelEdit">点击编辑</p>-->
					<el-switch
					  style="display: block"
					  v-model="editSwitch2"
					  active-color="#13ce66"
					  inactive-color="#ff4949"
					  @change="handelEdit2"
					  active-text="完成"
					  inactive-text="编辑">
					</el-switch>
				</div>
				<div class="textarea_input">
					<el-input
					  type="textarea"
					  :disabled="disabledTextArea2"
					  :maxlength=200
					  :rows="10"
					  placeholder="请输入内容"
					  v-model="textareaValue2">
					</el-input>
				</div>
			</div>
			
		</div>
	</div>
</template>
<script>
export default {
  name: 'hospital',
  data () {
    return {
      hospitalUrl: '',  //  医院图片放大存放路径
      dialogVisible: false,  //  图片放大
      hospitalUrl2: '',  //  医院营业执照放大存放路径
      dialogVisible2: false,  //  图片放大
      hospitalUrl3: '',  //  设备图片放大存放路径
      dialogVisible3: false,  //  图片放大
      textareaValue: 'please enter your value',  //  文本域的值,
      disabledTextArea: true,  // 文本域，默认完成
      editSwitch: true,  // 编辑开关， 默认完成
      textareaValue2: 'please enter your value',  //  文本域的值,
      disabledTextArea2: true,  // 文本域，默认完成
      editSwitch2: true  // 编辑开关， 默认完成
    }
  },
  computed: {
  },
  methods: {
    handleRemove (file, fileList) {  //  医院图片
      console.log(file, fileList)
    },
    handlePictureCardPreview (file) {
      this.hospitalUrl = file.url
      this.dialogVisible = true
    },
    handleRemove2 (file, fileList) {  //  营业执照
      console.log(file, fileList)
    },
    handlePictureCardPreview2 (file) {
      this.hospitalUrl2 = file.url
      this.dialogVisible2 = true
    },
    handleRemove3 (file, fileList) {  //  设备
      console.log(file, fileList)
    },
    handlePictureCardPreview3 (file) {
      this.hospitalUrl3 = file.url
      this.dialogVisible3 = true
    },
    handelEdit () {
      if (this.editSwitch === false) {
        this.disabledTextArea = false
      } else {
        this.disabledTextArea = true  //  完成编辑
        console.log(this.textareaValue)  //  打印文本的值
      }
    },
    handelEdit2 () {
      if (this.editSwitch2 === false) {
        this.disabledTextArea2 = false
      } else {
        this.disabledTextArea2 = true  //  完成编辑
        console.log(this.textareaValue2)  //  打印文本的值
      }
    }
  }
}
</script>
<style  lang="scss" scoped>
$mac:0.71;
.ym_content_hos {
  	width: 1200*$mac+px;
  	margin: 0 auto;
  	background-color: #f4f4f4;
  	/*margin-top: 37*$mac+px;*/
  	background-color: #fff;
  	padding: 20px;
  	}
  	.badge {
  	display: flex;
  	justify-content: flex-start;
  	span {
  		height: 35*$mac+px;
  		width: 3.5px;
  		background-color: #006FAF;
  		margin: 22*$mac+px 0;
  	}
  	p {
  		margin: 27*$mac+px 0 0 12*$mac+px;
  		font-size: 25*$mac+px;
  		color: #888;
  		font-family: ".PingFangSC-Medium"
  	}
  	
 }
  .el-icon-plus {
  		/*line-height: 281*$mac+px;
  		font-size: 40px;*/
  }
  .ym_pic {
  	margin: 20px 0;
  }
  
  .el-upload-list--picture-card .el-upload-list__item {
  	width: 372*$mac+px;
  	height: 281*$mac+px;
  }
  .ym_edit_hospitalProduce {
  	display: flex;
  	justify-content: space-between;
  	margin: 10px;
  	span {
  		font-size: 25*$mac+px;
  		color: #cacaca;
  		font-family: ".PingFangSC-Regular";
  	}
  	p {
  		color: #006FAF;
  		font-family: ".PingFangSC-Medium";
  		font-size: 25*$mac+px;
  	}
  }
  .textarea_input {
  	margin-top: 20px;
  }
  
  .hos_imgs div {
  	    width: 372*$mac+px !important;
	  	height: 281*$mac+px !important;
	  	display: inline-block !important;
  }
</style>



