<template>
	<div>
		<div class="ym_head">
			<div class="ym_content">
				<el-row>
					<el-col :span="6">
						<div class="grid-content" @click="rt('index', 0)">
							<div class="ym_logo"></div>
							<div class="ym_logo_text">啄木鸟牙医</div>
						</div>
					</el-col>
					<el-col :span="18">
						<div class="grid-content ym_nav">
							<router-link :to="n.pathId" v-for="n in getNavList" :key="n.key">{{ n.name }}</span> </router-link>
						</div>
					</el-col>
				</el-row>
			</div>
		</div>
	</div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  name: 'ymHead',
  data () {
    return {
    }
  },
  computed: {
    ...mapGetters(['getNavList'])
  },
  methods: {
  }
}
</script>
<style lang="scss" scoped>
$mac:0.71;
.el-row {
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }

  .ym_head {
  	width: 100%;
  	background-color: #fff;
  	height: 180*$mac+px;
  	border-bottom: 1px solid #ddd;
  }
  .ym_logo {
		width: 95*$mac+px;
		height: 95*$mac+px;
		background-size: 100% 100%;
		background-image: url('http://p2a8o3dk4.bkt.clouddn.com/ym_logo.jpg');
		margin: 42*$mac+px 0;
		display: inline-block;
	}
	.ym_logo_text {
		font-size: 20*$mac+px;
		color: #888888;
		font-family: '.PingFangSC-Semibold';
	}
  .grid-content {
  	height: 180*$mac+px;
  	line-height: 180*$mac+px;
    background-color: #fff;
    font-size: 20*$mac+px;
    display: flex;
    justify-content: flex-start;
  }
  .ym_nav {
  	justify-content: flex-end;
  	font-family: '.PingFangSC-Semibold';
  }
  a {
  	  margin: 0 0 0 38*$mac+px;
	  font-size: 20*$mac+px;
	  color: #888888;
  	  cursor: pointer;
	  text-decoration: none;
  }
  .ym_content {
  	width: 1200*$mac+px;
  	margin: 0 auto;
  	background-color: #f4f4f4;
  }
  .router-link-exact-active {
  		color: #006FAF;
  }
</style>


