<template>
	<div>
		<div class="ym_content">
			<div class="table_bar">
				<div class="table_bar_mod">
					<p v-for="(t, index) in getTableBarList" :class="[index === currentIndex ? activeClass : '']" @click="tog(index)">{{ t.text }}</p>
				</div>
				<div class="table_bar_icon">
					<el-button type="danger" icon="el-icon-delete">删除</el-button>
				</div>
			</div>
		    <div class="table_content">
			  <el-table
			    :data="tableData"
			    ref="singleTable"
			    border
			    highlight-current-row
			    @row-click="handleCurrent"
			    style="width: 100%">
			    <el-table-column
			      type="selection"
			      fixed>
			    </el-table-column>
			    <el-table-column
			      :prop="t.prop"
			      :sortable="t.prop === sort"
			      :label="t.name" v-for="(t, key) in getTableTitleList" :key="key">
			    </el-table-column>
			  </el-table>
			  <!-- 分页 -->
			  <div class="block">
			    <el-pagination
			      @size-change="handleSizeChange"
			      @current-change="handleCurrentChange"
			      :current-page.sync="currentPage2"
			      :page-sizes="[100, 200, 300, 400]"
			      :page-size="100"
			      layout="sizes, prev, pager, next"
			      :total="1000">
			    </el-pagination>
			  </div>
			</div>
		</div>
		<div>
			<el-dialog
			  title="订单详情"
			  :visible.sync="centerDialogVisible"
			  width="50%"
			  center>
			  <div class="dialog-content">
			  	<el-form :label-position="labelPosition" label-width="90px" :model="formLabelAlign">
				  <div class="form_content">
					  <el-form-item label="昵称">
					    <el-input v-model="formLabelAlign.name" :disabled="true"></el-input>
					  </el-form-item>
					  <el-form-item label="性别">
					    <el-input v-model="formLabelAlign.region" :disabled="true"></el-input>
					  </el-form-item>
				  </div>
				  <div class="form_content">
					  <el-form-item label="姓名">
					    <el-input v-model="formLabelAlign.name" :disabled="true"></el-input>
					  </el-form-item>
					  <el-form-item label="是否为急诊">
					    <el-input v-model="formLabelAlign.region" :disabled="true"></el-input>
					  </el-form-item>
				  </div>
				  <div class="form_content">
					  <el-form-item label="联系电话">
					    <el-input v-model="formLabelAlign.name" :disabled="true"></el-input>
					  </el-form-item>
					  <el-form-item label="支付途径">
					    <el-input v-model="formLabelAlign.region" :disabled="true"></el-input>
					  </el-form-item>
				  </div>
				  <div class="form_content">
					  <el-form-item label="年龄">
					    <el-input v-model="formLabelAlign.name" :disabled="true"></el-input>
					  </el-form-item>
					  <el-form-item label="预约时间">
					    <el-input v-model="formLabelAlign.region" :disabled="true"></el-input>
					  </el-form-item>
				  </div>
				  <div class="form_content">
					  <el-form-item label="预约医生">
					   <el-select v-model="value" placeholder="请选择" style="width: 179px;">
					    <el-option
					      v-for="item in options"
					      :key="item.value"
					      :label="item.label"
					      :value="item.value">
					    </el-option>
					  </el-select>
					  </el-form-item>
					  <el-form-item label="修改预约">
					  	<div>
					  		<el-date-picker
						      v-model="value1"
						      type="datetime"
						      placeholder="选择日期时间">
						    </el-date-picker>
					    </div>
					  </el-form-item>
				  </div>
				  <div class="form_content">
					  <el-form-item label="购买项目">
					    <el-input v-model="formLabelAlign.name"></el-input>
					  </el-form-item>
					  <el-form-item label="复诊次数">
					    <el-input v-model="formLabelAlign.region" :disabled="true"></el-input>
					  </el-form-item>
				  </div>
				  <div class="form_content">
					  <el-form-item label="支付金额">
					    <el-input v-model="formLabelAlign.name" :disabled="true"></el-input>
					  </el-form-item>
					  <el-form-item label="初诊时间">
					    <el-input v-model="formLabelAlign.region" :disabled="true"></el-input>
					  </el-form-item>
				  </div>
				  <div class="form_content">
					  <el-form-item label="已用优惠券">
					    <el-input v-model="formLabelAlign.name" :disabled="true"></el-input>
					  </el-form-item>
					  <el-form-item label="下单时间">
					    <el-input v-model="formLabelAlign.region" :disabled="true"></el-input>
					  </el-form-item>
				  </div>
				</el-form>
			  </div>
			  <span slot="footer" class="dialog-footer">
			    <el-button @click="disOrder">取消订单</el-button>
			    <el-button type="primary" @click="handelCloseDialogVisible">确 定</el-button>
			  </span>
			</el-dialog>
		</div>
	</div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  name: 'tableItem',
  data () {
    return {
      currentIndex: 0,  // 选项卡默认第一页
      activeClass: 'active',
      currentPage2: 5,  //  分页当前页
      sort: 'sick',  //  排序
      centerDialogVisible: false,  //  open dialog
      labelPosition: 'right',  //  排列方式
      formLabelAlign: {
        name: '',
        region: '',
        type: ''
      },
      tableData: [
        {
          dName: '小小',
          name: '王小虎',
          age: '18',
          orederTime: '2018-11-11 12:12:12'
        },
        {
          dName: '小小',
          name: '王小虎',
          age: '18',
          orederTime: '2018-11-11 12:12:12'
        }
      ],
      options: [
        {
          value: '选项1',
          label: '黄金糕'
        },
        {
          value: '选项2',
          label: '双皮奶'
        },
        {
          value: '选项3',
          label: '蚵仔煎'
        },
        {
          value: '选项4',
          label: '龙须面'
        },
        {
          value: '选项5',
          label: '北京烤鸭'
        }
      ],
      value: '',
      value1: '',
      value2: ''
    }
  },
  computed: {
    ...mapGetters(['getTableBarList', 'getTableTitleList'])
  },
  methods: {
    tog (index) {
      this.currentIndex = index
    },
    handleSizeChange (val) {  //  每页显示大小
      console.log(val)
    },
    handleCurrentChange (val) {  //  分页
      console.log(val)
    },
    handleCurrent (val) {  //  单选
      console.log(val)
      this.centerDialogVisible = true
    },
    handelCloseDialogVisible () {  //  弹出框确定按钮
      this.centerDialogVisible = false
    },
    disOrder () {  //  取消订单
      this.$confirm('此操作将取消该订单, 是否继续?', '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '取消成功!'
          })
          this.centerDialogVisible = false
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消失败'
          })
          this.centerDialogVisible = false
        })
    }
  }
}
</script>
<style lang="scss" scoped>
$mac:0.71;
.ym_content {
  	width: 1200*$mac+px;
  	margin: 0 auto;
  	background-color: #f4f4f4;
  	margin-top: 37*$mac+px;
  	.table_bar {
  		height: 51*$mac+px;
  		line-height: 51*$mac+px;
  		display: flex;
  		justify-content: space-between;
  		.table_bar_mod{
  			display: flex;
  		}
  		.table_bar_icon {
  			margin-bottom: 20px;
  			span{
  				font-size: 15*$mac+px;
  				font-family: 'PingFangSC-Medium';
  				margin-left: 22*$mac+px;
  			}
  		}
  		p{
  			text-align: center;
  			border-right:1px solid #c2c2c2;
  			padding: 0 40*$mac+px;
  			border-radius: 4*$mac+px;
  		    background: #fff;
  		    font-family: 'PingFangSC-Medium'
  		}
  		.active {
  			background-color: #006FAF;
  			color: #fff;
  		}
  	}
  	.table_content * {
  		font-family: 'PingFangSC-Medium';
  		font-size: 14*$mac+px;
  		
  	}
  	.block {
  			text-align: center;
  			margin-top: 43*$mac+px;
  		}
  }
  .dialog-content {
  	display: flex;
  	form {
  		width: 100%;
  	}
  }
 .form_content {
 	display: flex;
 	justify-content: space-between;
 }
 .el-form-item {
 	margin-bottom: 5px;
 }
 .el-date-editor.el-input, .el-date-editor.el-input__inner {
 	width: 179px;
 }
</style>


