<template>
	<div>
		<div class="ym_content">
			<div class="table_bar">
				<div class="table_bar_icon">
					<el-button type="danger" icon="el-icon-delete" @click="disOrder">删除</el-button>
					<el-button type="primary" icon="el-icon-plus">新增</el-button>
				</div>
			</div>
		    <div class="table_content">
			  <el-table
			    :data="tableData"
			    border
			    highlight-current-row
			    @row-click="handleCurrent"
			    style="width: 100%">
			    <el-table-column
			      type="selection"
			      fixed>
			    </el-table-column>
			    <el-table-column
			      :prop="t.prop"
			      :label="t.name" v-for="(t, key) in getDocTableTitleList" :key="key">
			    </el-table-column>
			  </el-table>
			  <!-- 分页 -->
			  <div class="block">
			    <el-pagination
			      @size-change="handleSizeChange"
			      @current-change="handleCurrentChange"
			      :current-page.sync="currentPage2"
			      :page-sizes="[100, 200, 300, 400]"
			      :page-size="100"
			      layout="sizes, prev, pager, next"
			      :total="1000">
			    </el-pagination>
			  </div>
			</div>
		</div>
		<!--弹出框-->
		<div>
			<el-dialog
			  title="订单详情"
			  :visible.sync="centerDialogVisible"
			  width="50%"
			  center>
			  <div class="dialog-content">
			  	<el-form :label-position="labelPosition" label-width="90px" :model="formLabelAlign">
			  		<div class="doc_head_img">
			  			<img src="" alt="" />
			  		</div>
				  <div class="form_content">
				  	<div style="width: 50%;">
						  <el-form-item label="姓名">
						    <el-input v-model="formLabelAlign.name" :disabled="editSwitch"></el-input>
						  </el-form-item>
						  <el-form-item label="性别">
						   <el-select v-model="value" placeholder="请选择" style="width: 100%;" :disabled="editSwitch">
						    <el-option
						      v-for="item in options"
						      :key="item.value"
						      :label="item.label"
						      :value="item.value">
						    </el-option>
						  </el-select>
						  </el-form-item>
						  <el-form-item label="联系电话">
						    <el-input v-model="formLabelAlign.region" :disabled="editSwitch"></el-input>
						  </el-form-item>
						 <el-form-item label="年龄">
						    <el-input v-model="formLabelAlign.region" :disabled="editSwitch"></el-input>
						  </el-form-item>
						  <el-form-item label="最高学历">
						   <el-select v-model="value1" placeholder="请选择" style="width: 100%;" :disabled="editSwitch">
						    <el-option
						      v-for="item in options1"
						      :key="item.value"
						      :label="item.label"
						      :value="item.value">
						    </el-option>
						  </el-select>
						  </el-form-item>
						  <el-form-item label="从业年限">
						   <el-select v-model="value2" placeholder="请选择" style="width: 100%;" :disabled="editSwitch">
						    <el-option
						      v-for="item in options2"
						      :key="item.value"
						      :label="item.label"
						      :value="item.value">
						    </el-option>
						  </el-select>
						  </el-form-item>
						  <el-form-item label="擅长">
						  	<el-button v-for="(t, key) in tagList" :class="[t.checked === true? tagActive: '']" :key="key" style="margin:0 5px;" @click.native="handelChangeTagState(t)" size="small" :disabled="editSwitch">{{ t.content }}</el-button>
						  </el-form-item>
					  </div>
					  <div>
					  	医生介绍
					  	<div class="textarea_input">
							<el-input
							  type="textarea"
							  :disabled="editSwitch"
							  :rows="12"
							  placeholder="请输入内容"
							  v-model="textareaValue">
							</el-input>
						</div>
					  </div>
				  </div>
				</el-form>
			  </div>
			  <span slot="footer" class="dialog-footer">
			    <el-button @click="handelEditDoctor">编辑</el-button>
			    <el-button type="primary" @click="handelCloseDialogVisible">确 定</el-button>
			  </span>
			</el-dialog>
		</div>
	</div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  name: 'tableItem',
  data () {
    return {
      tagActive: 'tagActive',  //  标签样式
      currentPage2: 1,  //  当前页
      textareaValue: 'please enter your value',  //  文本域的值,
      disabledTextArea: true,  // 文本域，默认完成
      editSwitch: true,  // 编辑开关， 默认完成
      centerDialogVisible: false,  //  open dialog
      labelPosition: 'right',  //  排列方式
      formLabelAlign: {
        name: '',
        region: '',
        type: ''
      },
      tableData: [  //  列表数据
        {
          dName: '小小',
          name: '王小虎',
          age: '18',
          orederTime: '2018-11-11 12:12:12'
        },
        {
          dName: '小小',
          name: '王小虎',
          age: '18',
          orederTime: '2018-11-11 12:12:12'
        }
      ],
      options: [  //  性别
        {
          value: '选项1',
          label: '黄金糕'
        },
        {
          value: '选项2',
          label: '双皮奶'
        }
      ],
      value: '',
      options1: [  //  最高学历
        {
          value: '选项1',
          label: '黄金糕'
        },
        {
          value: '选项2',
          label: '双皮奶'
        }
      ],
      value1: '',
      options2: [  // 从业年限
        {
          value: '选项1',
          label: '黄金糕'
        },
        {
          value: '选项2',
          label: '双皮奶'
        }
      ],
      value2: '',
      tagList: [  //  标签 擅长
        {
          content: '美白',
          checked: true
        },
        {
          content: '整形',
          checked: false
        },
        {
          content: '美白',
          checked: false
        },
        {
          content: '美白',
          checked: true
        },
        {
          content: '美白',
          checked: true
        },
        {
          content: '美白',
          checked: true
        }
      ]
    }
  },
  computed: {
    ...mapGetters(['getDocTableTitleList'])
  },
  methods: {
    handleSizeChange (val) {
      console.log(val)
    },
    handleCurrentChange (val) {
      console.log(val)
    },
    handleCurrent (val) {  //  点击开启弹窗
      this.centerDialogVisible = true
    },
    handelEditDoctor () {  // 编辑医生
      this.editSwitch = false
    },
    handelCloseDialogVisible () {  //  确定编辑
      if (this.editSwitch === true) {  //  是否开启编辑
        this.centerDialogVisible = false
      } else {
        //  请求数据库
        this.editSwitch = true
        this.centerDialogVisible = false   // 关闭弹窗
      }
    },
    handelChangeTagState (v) {  //  改变标签状态
      if (v.checked === true) {
        v.checked = false
      } else {
        v.checked = true
      }
    },
    disOrder () {  //  取消订单
      this.$confirm('此操作将取消该订单, 是否继续?', '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '取消成功!'
          })
          this.centerDialogVisible = false
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消失败'
          })
          this.centerDialogVisible = false
        })
    }
  }
}
</script>
<style lang="scss" scoped>
$mac:0.71;
.ym_content {
  	width: 1200*$mac+px;
  	margin: 0 auto;
  	background-color: #f4f4f4;
  	margin-top: 37*$mac+px;
  	.table_bar {
  		height: 51*$mac+px;
  		line-height: 51*$mac+px;
  		display: flex;
  		justify-content: flex-end;
  		.table_bar_mod{
  			display: flex;
  		}
  		.table_bar_icon {
  			span{
  				font-size: 15*$mac+px;
  				font-family: 'PingFangSC-Medium';
  				margin-left: 22*$mac+px;
  			}
  		}
  		p{
  			text-align: center;
  			border-right:1px solid #c2c2c2;
  			padding: 0 40*$mac+px;
  			border-radius: 4*$mac+px;
  		    background: #fff;
  		    font-family: 'PingFangSC-Medium'
  		}
  		.active {
  			background-color: #006FAF;
  			color: #fff;
  		}
  	}
  	.table_content * {
  		font-family: 'PingFangSC-Medium';
  		font-size: 14*$mac+px;
  		margin-top: 10px;
  		
  	}
  	.block {
  			text-align: center;
  			margin-top: 43*$mac+px;
  		}
  }
  .dialog-content {

  	form {
  		width: 100%;
  	}
  }
 .form_content {
 	display: flex;
 	justify-content: space-between;
 }
 .el-form-item {
 	margin-bottom: 5px;
 }
 .el-date-editor.el-input, .el-date-editor.el-input__inner {
 	width: 179px;
 }
 .doc_head_img {
 	width: 97*$mac+px;
 	height: 97*$mac+px;
 	background-color: #444444;
 	border-radius: 50%;
 	margin: 0px auto 30px;
 }
 .tagActive {
 	background-color: #006FAF;
 	color: #fff;
 }
</style>


