<template>
	<div>
		<div class="ym_content">
			<div>
				<div class="choose_month">
					<el-button size="small">全部</el-button>
					<el-button size="small">好评</el-button>
					<el-button size="small">中评</el-button>
					<el-button size="small">差评</el-button>
					<div>
					   <div class="block">
						  <span class="demonstration" style="margin-left: 10px;">筛选月份</span>
						  <el-date-picker
						    v-model="month"
						    type="month"
						    placeholder="选择月">
						  </el-date-picker>
						</div>
					</div>
				</div>
			</div>
			
			<el-row v-for="e in evaList" :key="e.key">
				<el-col :span="2">
				   <img src="http://p2fkgzxqv.bkt.clouddn.com/icon04.png" class="eva_head_img"/>
				</el-col>
				<el-col :span="22">
					<div class="eva_card">
						<div>
							<span>{{ e.name }}
							  <i>{{ e.sexy }}</i>
							</span>
						</div>
						<div>
							<el-rate
							  v-model="e.rateVal"
							  disabled
							  show-score
							  text-color="#ff9900"
							  score-template="{value}">
							</el-rate>
						</div>
					</div>
					<div class="eva_hos_pro">
						{{ e.evaHosPro }}
					</div>
					<div class="eva_img">
						<template>
						  <el-carousel :interval="4000" type="card" height="200px">
						    <el-carousel-item v-for="item in e.evaUrl" :key="item.url">
						      <img :src="item.url" />
						    </el-carousel-item>
						  </el-carousel>
						</template>
						
					</div>
					<div class="eva_card">
						<span>{{ e.evaDate }}</span>
						<el-button class="eva_reback" @click.native="handelReBack(e)">回复</el-button>
					</div>
					<div>
						<el-row>
							<el-col :span="2" class="eva_buiness_reback">商家回复:</el-col>
							<el-col :span="22" class="eva_hos_pro">
								{{ e.evaTextArea }}
							</el-col>
						</el-row>
						<span class="eva_buiness_reback_time">{{ e.evaReDate }}</span>
					</div>
					<div v-if="e.showEvaArea === true">
						<el-input
						  type="textarea"
						  :rows="4"
						  placeholder="商家回复"
						  v-model="e.evaTextArea">
						</el-input>
						<div style="text-align: center;margin: 10px 0;">
							<el-button type="primary" @click.native="handelSuccessEva(e)">完成</el-button>							
						</div>
					</div>
				</el-col>
			</el-row>
		</div>
	</div>
</template>
<script>
export default {
  name: 'eva',
  data () {
    return {
      month: '',  //  选择月
      evaList: [
        {
          rateVal: 4,
          name: '西门吹牛',
          sexy: 'boy',
          head_img: 'http://p2fkgzxqv.bkt.clouddn.com/icon04.png',
          evaHosPro: '咚咚咚',  //  医院介绍
          evaDate: new Date(),  // 评价时间
          evaReDate: '',  // 回复时间
          evaTextArea: '哈哈哈哈或或😁',
          showEvaArea: false,
          month: 9,
          evaUrl: [  //  图片
            {
              url: 'http://p2a8o3dk4.bkt.clouddn.com/login1.gif'
            },
            {
              url: 'http://p2a8o3dk4.bkt.clouddn.com/login2.gif'
            },
            {
              url: 'http://p2a8o3dk4.bkt.clouddn.com/login3.gif'
            }
          ]
        },
        {
          rateVal: 4.5,
          name: '西门吹牛',
          sexy: 'boy',
          head_img: 'http://p2fkgzxqv.bkt.clouddn.com/icon04.png',
          evaHosPro: '咚咚咚',  //  医院介绍
          evaDate: new Date(),  // 评价时间
          evaReDate: '',  // 回复时间
          evaTextArea: '哈哈哈哈或或😁',
          showEvaArea: false,
          month: 9,
          evaUrl: [  //  图片
            {
              url: 'http://p2a8o3dk4.bkt.clouddn.com/login1.gif'
            },
            {
              url: 'http://p2a8o3dk4.bkt.clouddn.com/login2.gif'
            },
            {
              url: 'http://p2a8o3dk4.bkt.clouddn.com/login3.gif'
            }
          ]
        }
      ]
    }
  },
  computed: {
  },
  methods: {
    handelReBack (e) {
      e.showEvaArea = true
    },
    handelSuccessEva (e) {
      e.showEvaArea = false  //  完成编辑
      e.evaReDate = new Date()
    }
  }
}
</script>
<style lang="scss" scoped>
$mac:0.71;
.ym_content {
  	width: 1200*$mac+px;
  	margin: 0 auto;
  	background-color: #f4f4f4;
  	/*margin-top: 37*$mac+px;*/
  	padding: 0 50px;
  	background-color: #fff;
  	.choose_month {
	    padding: 37*$mac+px 0;
  		display: flex;
  		justify-content: flex-end;
  	}
  	.eva_card {
  		display: flex;
  		justify-content: space-between;
  		margin-bottom: 20px;
  	}
  	.eva_head_img {
  		width: 75*$mac+px;
  		height:75*$mac+px;
  		border-radius: 50%;
  		background-color: #000000;
  	}
  	.el-carousel__item img {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
    width: 100%;
    height: 200px;
  }
  
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
  .eva_hos_pro {
  	color: #666666;
  	font-family: "PingFangSC-Regular";
  	font-size: 15*$mac+px;
  	line-height: 2em;
  }
  .eva_reback {
  	color: #006FAF;
  	font-family: ".PingFangSC-Medium";
  	font-size: 22*$mac+px;
  }
  .eva_buiness_reback {
  	font-family: ".PingFangSC-Regular";
  	color: #000;
  	font-size: 17*$mac+px;
  	line-height: 2em;
  }
  .eva_buiness_reback_time {
  	margin: 10px 0 30px 0;
  	display: inline-block;
  }
}

</style>



